<template>
    <div v-if="uploading" style="
            width: 100%;
            height:100%">
        <v-progress-circular
                :rotate="-90"
                :size="100"
                :width="15"
                :value="uploadPercentage"
                class="center"
                color="primary"
        >
            {{uploadPercentage+'%'}}
        </v-progress-circular>
    </div>
    <v-container class="pa-0 ma-0" fluid v-else>
        <v-row no-gutters dense>
            <v-col>
                <v-spacer></v-spacer>
                <v-container class="pa-0 ma-0">
                    <v-row class="d-flex">
                        <v-col class="flex-grow-0">
                            <v-tabs
                                    v-model="tab"
                                    vertical
                                    style="width: available"
                            >
                                <v-tab
                                        v-for="item in antiques"
                                        :key="item.key"
                                >
                                    {{ (item.name === undefined || item.name === '')?'':item.name }}<v-btn @click="antiques.splice(antiques.indexOf(item),1)" icon small><v-icon>mdi-close</v-icon></v-btn>
                                </v-tab>
                            </v-tabs>

                        </v-col>
                        <v-col>
                            <v-row no-gutters dense>
                                <v-col>
                                    <v-toolbar flat >
                                    <v-spacer/>
                                    <v-btn
                                            color="primary"
                                            dark
                                            class="mb-2"
                                            @click="addItem"
                                            style="margin-right: 1em"
                                    ></v-btn>

                                    <v-btn class="mb-2"  @click="dialog = true" color="primary" :disabled="!valid">
                                        
                                        <v-icon right>mdi-exit-to-app</v-icon>
                                    </v-btn>
                                    </v-toolbar>
                                </v-col>
                            </v-row>
                            <v-row no-gutters dense>
                                <v-col>
                                    <v-form v-model="valid">
                                        <v-tabs-items vertical v-model="tab">
                                            <v-tab-item
                                                    v-for="item in antiques"
                                                    :key="item.key"
                                            >
                                                <new-antique-info-input :antique="item"/>
                                            </v-tab-item>
                                        </v-tabs-items>
                                    </v-form>
                                </v-col>
                            </v-row>
                        </v-col>
                    </v-row>
                </v-container>
            </v-col>
        </v-row>
        <applier-info-import-dialog v-model="dialog" @success="upload" :applier="applier" />
    </v-container>
</template>

<script>

    import {AntiqueClient} from "../client/AntiqueClient";
    import NewAntiqueInfoInput from "./classic/NewAntiqueInfoInput";
    import ApplierInfoImportDialog from "./ApplierInfoImportDialog";

    export default {
        components: {ApplierInfoImportDialog, NewAntiqueInfoInput},
        data: () => ({
            headers: [
                {
                    text: '',
                    align: 'start',
                    sortable: false,
                    value: 'name',
                },
                {text: '', value: 'desp'},
                {text: '', value: 'type'},
                { text: '', value: 'pic' },
                { text: '', value: 'actions', sortable: false },
                {text: '', value: 'data-table-expand'}
            ],
            antiques: [{key:0}],
            nameRules:[v=> v.length !== 0 || ''],
            despRules:[v=> v.length !== 0 || ''],
            picRules:[v=> v !== undefined || ''],
            valid:false,
            uploading:false,
            uploadPercentage:0,
            tab:0,
            dialog:false,
            applier:{}
        }),
        name: "BatchImportPage",
        methods:{
            deleteItem (item) {
                const index = this.antiques.indexOf(item)
                confirm('?') && this.antiques.splice(index, 1)
            },
            addItem(){
                // eslint-disable-next-line @typescript-eslint/no-this-alias
                const model = this
                this.antiques.push({
                    key:this.antiques.length
                })
            },
            async upload() {
                this.uploading = true
                let finished = 0;
                for(const antique of this.antiques) {
                    const res = await AntiqueClient.postAntique(antique)
                    await AntiqueClient.postEnterpriseApplierInfo(res.id, this.applier)
                    finished++;
                    this.uploadPercentage = (finished / this.antiques.length) * 100
                }
                this.$router.go(-1)
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "style/layout";
</style>
